<template>
    <section class="chart">
        <el-row>
            <el-col :span="12">
                <div id="chartColumn" style="width:100%; height:400px;"></div>
            </el-col>
            <el-col :span="12">
                <div id="chartPie" style="width:100%; height:400px;"></div>
            </el-col>
        </el-row>
    </section>
</template>

<script>
    import echarts from 'echarts'

    export default{
        name:'count',
        data (){
            return {
                chartColumn:null,
                chartPie:null
            }
        },
        mounted:function(){
            var _this=this;
            //domecharts
            this.chartColumn = echarts.init(document.getElementById('chartColumn'));
            this.chartPie = echarts.init(document.getElementById('chartPie'));

            this.chartColumn.setOption({
                title: { text: '' },
                tooltip: {},
                xAxis: {
                    data: ["","","","","",""]
                },
                yAxis: {},
                series: [{
                    name: '',
                    type: 'bar',
                    data: [45, 20, 76, 10, 10, 20]
                }]
            });


            this.chartPie.setOption({
                title : {
                    text: '',
                    subtext: '',
                    x:'center'
                },
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: ['','','','','']
                },
                series : [
                    {
                        name: '',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data:[
                            {value:335, name:''},
                            {value:310, name:''},
                            {value:234, name:''},
                            {value:135, name:''},
                            {value:1548, name:''}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            });
        }

    }
</script>

<style scoped>
    .chart {
        width: 100%;
        float: left;
    }
</style>



