// mock.js mockjs
import Mock from 'mockjs';


//
const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: '',
    name: ''
  }
];

//
const Users = [];

//86for
for (let i = 0; i < 86; i++) {
  //Mock.mock()id
  Users.push(Mock.mock({
    /**
     * Random.guid()GUIDGUID
     * GUID
     * id
     */
    id: Mock.Random.guid(),

    /**
     *Random.chineseName()Random.cname()
     *
     */
    name: Mock.Random.cname(),

    //mock('@county(true)')
    addr: Mock.mock('@county(true)'),

    //18601
    'age|18-60': 1,

    //Random.date()
    birth: Mock.Random.date(),

    //Random.integer(0, 1)01
    sex: Mock.Random.integer(0, 1)
  }));
}

//LoginUsers  Users
export { LoginUsers, Users };



