<template>
  <div class="table_list">
    <section class="path_box">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </section>
    <section class="table_list_content">
      <!---->
      <el-col :span="24" class="toolbar" style="margin-top: 20px">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.name" placeholder="" style="width:200px"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="getUsers" icon="search"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click="handleAdd" icon="plus"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <e-col class="list_content">
        <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column prop="name" label="" width="120" sortable></el-table-column>
          <el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable></el-table-column>
          <el-table-column prop="age" label="" width="100" sortable></el-table-column>
          <el-table-column prop="birth" label="" width="120" sortable></el-table-column>
          <el-table-column prop="addr" label="" min-width="180" sortable></el-table-column>
          <el-table-column inline-template :context="_self" label="" width="150">
            <span>
              <el-button size="small" @click="handleEdit(row)" ></el-button>
              <el-button type="danger" size="small" @click="handleDel(row)" ></el-button>
            </span>
          </el-table-column>
        </el-table>
      </e-col>

      <!---->
      <el-col :span="24" class="page">
        <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="20"
        :total="total"
        style="float:right;"
        >
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
          <el-form-item label="" prop="name">
            <el-input v-model="editForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="editForm.sex">
              <el-radio class="radio" :label="1"></el-radio>
              <el-radio class="radio" :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="editForm.addr"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"> </el-button>
          <el-button
          type="primary"
          @click.native="editSubmit"
          :loading="editLoading"
          >{{btnEditText}}</el-button>
        </div>
      </el-dialog>
    </section>

  </div>

</template>
<script>
	import util from '../common/util'
  import NProgress from 'nprogress'
	import { getUserListPage, removeUser, editUser, addUser } from '../api/api';

	export default {
    name:'List',
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				editFormVisible: false,//
				editFormTtile: '',//
				//
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},
				editLoading: false,
				btnEditText: ' ',
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				}
			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
        //
				getUserListPage(para).then((res) => {
          //
					this.total = res.data.total;
          //
					this.users = res.data.users;
          //loadding
					this.listLoading = false;
				});
			},
			//
			handleDel: function (row) {
				var _this = this;
				this.$confirm('?', '', {
				}).then(() => {
					_this.listLoading = true;
					let para = { id: row.id };
					removeUser(para).then((res) => {
						_this.listLoading = false;
						_this.$notify({
							title: '',
							message: '',
							type: 'success'
						});
						_this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (row) {
				this.editFormVisible = true;
				this.editFormTtile = '';
				this.editForm.id = row.id;
				this.editForm.name = row.name;
				this.editForm.sex = row.sex;
				this.editForm.age = row.age;
				this.editForm.birth = row.birth;
				this.editForm.addr = row.addr;
			},
			// or 
			editSubmit: function () {
				var _this = this;
				_this.$refs.editForm.validate((valid) => {
					if (valid) {
						_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						_this.btnEditText = '';
							if (_this.editForm.id == 0) {
								//
								let para = {
									name: _this.editForm.name,
									sex: _this.editForm.sex,
									age: _this.editForm.age,
									birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
									addr: _this.editForm.addr,
								};
								addUser(para).then((res) => {
									_this.editLoading = false;
									_this.btnEditText = ' ';
									_this.$notify({
										title: '',
										message: '',
										type: 'success'
									});
									_this.editFormVisible = false;
									_this.getUsers();
								});
							} else {
								//
								let para = {
									id: _this.editForm.id,
									name: _this.editForm.name,
									sex: _this.editForm.sex,
									age: _this.editForm.age,
									birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
									addr: _this.editForm.addr,
								};
								editUser(para).then((res) => {
									_this.editLoading = false;
									_this.btnEditText = ' ';
									_this.$notify({
										title: '',
										message: '',
										type: 'success'
									});
									_this.editFormVisible = false;
									_this.getUsers();
								});
							}
						});
					}
				});
			},
			//
			handleAdd: function () {
				var _this = this;
				this.editFormVisible = true;
				this.editFormTtile = '';
				this.editForm.id = 0;
				this.editForm.name = '';
				this.editForm.sex = 1;
				this.editForm.age = 0;
				this.editForm.birth = '';
				this.editForm.addr = '';
			}
		},
		mounted() {
			this.getUsers();
		}
	}
</script>

<style lang="css">
  .path_box{
    width: calc(100% - 24px);
    position: absolute;
    z-index: 999;
    background-color: #fff;
    padding: 18px 12px;
    border-top:1px solid #f3f3f3;
    border-bottom:1px solid #f3f3f3;
  }
  .table_list_content{
    background-color: #fff;
    width: calc(100% - 24px);
    padding: 0px 12px 12px 12px;
    position: absolute;
    top: 50px;
  }
  .page{
    float:right;
    margin-top: 12px;
  }
  .list_content{
    width: 100%;
    float: left;
    margin-top: -28px;
  }

</style>



